.App{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    padding: 4rem 0;
    background-color: rgb(176, 218, 231);
}

.container{
    background-color: white;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    min-width: 30vw;
    min-height: 50vh;
    border-radius: .3rem;
    box-shadow: 0 1.2rem 2.2rem .1rem rgba(0, 0, 0, 0.3);
    overflow: hidden;
    scrollbar-width: none;
}

:root{
    --main-color:rgb(90, 111, 117);
}