.task-item{
    display: flex;
    justify-content: space-between;
    width: 100%;
    background-color: rgb(225, 243, 243);
    padding: 1.4rem;
    box-shadow: 0 .5rem .6rem .1rem rgba(0, 0, 0, 0.3);
    border-left: .5rem solid transparent;
    cursor: pointer;
}

.task-item:not(:last-child){
    margin-bottom: 1.5rem;
}

.task-item__title{
    text-transform: capitalize;
    font-weight: 600;
    font-size: 1.4rem;
    margin-bottom: .5rem;
    color: rgb(90, 111, 117);
}

.task-item__title--empty{
    display: flex;
    justify-content: center;
    font-size: 2rem;
    margin-top: 7vh;
}

.task-item__text{
    font-size: 1.2rem;
    color: rgb(90, 111, 117);
}

.task-item__active{
    border-left: .5rem solid rgb(96, 167, 189);
}

.task-btn{
    border: solid .2rem orangered;
    display: inline-block;
    padding: .6rem 1.8rem;
    border-radius: .6rem;
    margin-left: 4rem;
    font-size: 1rem;
    text-transform: uppercase;
    color: orangered;
    font-weight: 700;
    transition: all .2s;
}

.task-btn:hover{
    border: solid .2rem transparent;
    background-color: orangered;
    color: white;
}