.header{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    padding: 2.4rem;
    border-bottom: .2rem solid rgb(176, 218, 231);
}

.header-container{
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.header-title{
    font-size: 2rem;
    text-transform: capitalize;
    font-weight: 500;
    color: var(--main-color);
    margin-bottom: .5rem;
}

.header-btn{
    color: white;
    text-transform: uppercase;
    font-size: 1.2rem;
    font-weight: 600;
    display: inline-block;
    padding: 1rem 2rem;
    border-radius: .3rem;
    background-color: rgb(96, 167, 189);
    border: none;
    box-shadow: 0 .5rem 1rem 0 rgba(96, 167, 189, .6);
    transition: transform .2s;
}

.header-btn:hover{
    transform: scale(1.1);
}

.header-btn:active{
    transform: scale(.9);
    opacity: .6;
    box-shadow: 0 .5rem .5rem 0 rgba(96, 167, 189, .8);
}

.input-header{
    outline: none;
    border: solid .2rem rgba(96, 167, 189, .8);
    padding: .6rem .9rem;
    border-radius: .4rem;
}

.input-header:not(:last-child){
    margin-bottom: 1rem;
}

.btn--alert{
    background-color: orangered;
}