.form{
    
    display: flex;
    height: 0;
    overflow: hidden;
    flex-direction: column;
    width: 100%;
    margin-top: 2rem;
    transition: all .2s;
}

.form__label{
    text-transform: capitalize;
    font-size: 1.4rem;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    color: var(--main-color);
}

.form__label--checkbox{
    flex-direction: row;
    justify-content: space-around;
    align-content: center;
    padding: .5rem;
    
}

.form__label:not(:last-child){
    margin-bottom: 1.5rem;
}

.form__input{
    border-radius: .3rem;
    padding: .5rem .9rem;
    outline: none;
    border: solid .3rem var(--main-color);
}

.btn--submit{
    border: none;
    background-color: var(--main-color);
    color: white;
    display: inline-block;
    padding: .9rem 1.8rem;
    width: 80%;
    align-self: center;
    border-radius: .4rem;
    box-shadow: 0 .5rem 1rem 0 rgba(90, 111, 117, .6);
    transition: all .3s;
}

.btn--submit:hover{
    transform: scale(1.1);
}

.btn--submit:active{
    transform: scale(.9);
    opacity: .8;
}

.active{
    height: 35vh;
}